import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';


const routes: Routes = [
  {path: '',redirectTo: 'user',pathMatch: 'full'},
  {
    path: 'organaization',
    loadChildren: () =>
      import('./donation-org/donation-org.module').then(
        (m) => m.DonationOrgModule
      ),
  },
  {
    path: 'donation',
    loadChildren: () =>
      import('./donation/donation.module').then((m) => m.DonationModule),
  },

  {
    path: 'donor',
    loadChildren: () =>
      import('./donor/donor.module').then((m) => m.DonorModule),
  },
  {
    path: 'madni_basta',
    loadChildren: () =>
      import('./madni-basta/madni-basta.module').then(
        (m) => m.MadniBastaModule
      ),
  },
  {
    path: 'transaction',
    loadChildren: () =>
      import('./transaction/transaction.module').then(
        (m) => m.TransactionModule
      ),
  },
  {
    path: 'error',
    loadChildren: () =>
      import('./error-x/error-x-routing.module').then(
        (m) => m.ErrorXRoutingModule
      ),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FeatureRoutingModule {}
